#navbar{
    top: 0px;
    position: fixed;
    z-index: 9999;

    display: flex;

    justify-content: space-between;
    align-items: center;

    width: 100%;

    background-color: #352F44;
    color: #EEEEEE;

    box-sizing: border-box;
    padding: 15px 75px;

}

#navbar section{
    display: flex;
    justify-content: center;
    gap: 50px;
}

#navbar section a{
    color: rgb(250, 240, 230);
    text-decoration: none;
}

#navbar i{
    display: flex;
    gap: 10px;
}

#navbar i p{
    font-weight: 300;
}