#app{
  width: 100%;
  height: 100vh;

  background-color: rgb(49, 54, 63);
}

#hero{
  width: 100%;
  min-height: 75vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;

  background-image: linear-gradient(rgba(0, 0, 0, .5), rgb(53, 47, 68) 90%), url(../code.png);
  background-size: cover;
  background-position: center;
  background-size: 120%;
  background-repeat: no-repeat;

  gap: 0px;

  padding: 0px 30%;
  padding-bottom: 75px;
  box-sizing: border-box;

  text-align: center;
}

#hero h1{
  color: rgb(250, 240, 230);
  text-transform: uppercase;

  font-size: 90px;
  font-weight: 600;

  letter-spacing: 20px;
}

#hero h2, #hero h2 a{
  color: rgb(250, 240, 230);
  text-transform: uppercase;

  font-size: 15px;
  font-weight: 400;
}

#interested{
  text-decoration: none;
  color: white;

  background-color: rgb(92, 84, 112);
  padding: 15px 75px;

  border-radius: 5px;
  border: 0px solid #222831;

  text-transform: capitalize;

  margin-top: 20px;
  margin-bottom: 50px;
}

#interested:hover{

}

#projects{
  width: 100%;
  min-height: 15vh;

  padding: 0px 400px;
  box-sizing: border-box;

  position: absolute;

  display: flex;
  list-style: none;

  transform: translateY(-50%);
}

#projects li{
  width: 100%;

  background-color: rgb(92, 84, 112);
  background-color: rgb(53, 47, 68);

  display: flex;
  gap: 0px;

  justify-content: center;
  align-items: center;

  color: rgb(250, 240, 230);
}

#projects li:nth-child(2){
  background-color: rgb(53, 47, 68);
  background-color: rgb(92, 84, 112);
}

#projects i{
  font-size: 55px;

  padding: 15px;
}

#projects h1{
  text-transform: capitalize;
  font-size: 20px;
}

#projects p{
  text-transform: capitalize;
  font-size: 25px;
}


#about{
  height: 100vh;
  background-color: rgb(92, 84, 112);
}
